import splitter from "json-string-splitter";
export function parseLog(raw) {
    try {
        var lines = raw.split('\n');
        return lines.map(tryParseJSON);
    }
    catch (e) {
        console.log("falling back to json splitter");
        // the input might have had its data reformatted, breaking the original newline separated json
        var lines = splitter(raw).jsons;
        return lines.map(tryParseJSON);
    }
}
function tryParseJSON(json) {
    try {
        return JSON.parse(json);
    }
    catch (e) {
        // fix unescaped message json
        var startPos = json.indexOf('"message":"') + ('"message":"').length;
        var endPos = json.lastIndexOf('","level":');
        var start = json.substring(0, startPos);
        var end = json.substring(endPos);
        var message = json.substring(startPos, endPos - startPos);
        var escapedMessage = message.replace(/([^\\]|^)["]/g, '$1\\"');
        json = start + escapedMessage + end;
        try {
            return JSON.parse(json);
        }
        catch (e) {
            console.error('Error while parsing log message:');
            throw e;
            // console.log(json);
            // console.error(e);
        }
    }
}
